import {createDataModule} from "@/lib/dataModules/BaseDataCenter"

var config = {
    enumeration: {
        sourceType: {
            //:USER(,/user); ORDER(,/order); ITEM(,/item); BRAND(,/brand); RICH_TEXT(,/rich_text); OUTSIDE(,/OUTSIDE).
            user: "USER",
            order: "ORDER",
            item: "ITEM",
            brand: "BRAND",
            richText: "RICH_TEXT",
            outside: "OUTSIDE",
            message: "MESSAGE"
        },

        attachType: {

            //:IMG(,[.jpg, .jpeg, .png],BUCKET_IMG); VIDEO(,[.mp3],BUCKET_IMG); FILE(,[.*],BUCKET_IMG).
            img: "IMG",
            video: "VIDEO",
            file: "FILE"
        }
    },

    getAttachByAssignId: {
        url: "attach/queryByAttachAssignId",
        path: "uploadapi/"
    },

    maintainAssignId: {
        url: "attach/saveOrUpdateReturnAssignId",
        path: "uploadapi/",
        paras: {
            attachAssignId: "",
            attachList: ""
        }
    },

    upload: {
        url: "attach/upload",
        path: "uploadapi/",
        contentType: "file",
        paras: {
            attachType: "",
            source: ""
        }
    }
};
export default createDataModule(config, "attachment")



